pre,
code {
  font-family: 'Ubuntu Mono', monospace;
  font-weight: 800;
  line-height: 1.2;
}
pre {
  border: none;
  background-color: black;
  color: white !important;
  padding: 24px;
  border-radius: 4px;
  letter-spacing: 0.4px;
  font-size: 16px;
  margin: 32px 0;
}
pre code {
  background-color: transparent;
  padding: 0;
}
code {
  background-color: #ffe4f3;
  background-color: #eeddff;
  border-radius: 4px;
  padding: 4px 8px;
}
th code {
  background-color: black;
}

:root {
  --primary: blueviolet;
  --secondary: #ff2da4;
  --white: #ffffff;
}
.blueviolet {
  color: blueviolet;
  color: var(--primary);
}
.bg-blueviolet {
  background-color: blueviolet;
  background-color: var(--primary);
}
.gray {
  color: darkgray;
}

.menu .container {
  padding: 16px 0;
}

.menu .flex {
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.menu li {
  list-style: none;
  font-size: 12px;
  padding-left: 4px;
}
.menu a {
  padding: 24px 0;
  color: white;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 2px;
  /*  display: flex; */
  -webkit-align-items: center;
          align-items: center;
  max-width: 64px;
  border-radius: 4px;
  padding: 8px;
  padding-right: 12px;
}
.menu a:hover {
  background-color: var(--secondary);
  transition-duration: 350ms;
}

.menu img {
  max-width: 42px;
  -webkit-filter: brightness(0) invert(1);
          filter: brightness(0) invert(1);
}

/* fix for side menu */
.menu .container {
  position: fixed;
  height: 100%;
  -webkit-flex-direction: column;
          flex-direction: column;
  background-color: black;
  top: 0;
  box-sizing: border-box;
  text-align: center;
  padding: 36px 4px;
}
.menu a {
  display: static;
}

/* progress bar */
progress {
  height: 5px;

  /* Reset the appearance */
  -webkit-appearance: none;
  appearance: none;

  /* Get rid of the default border in Firefox/Opera. */
  border: none;

  /* Progress bar container for Firefox/IE10+ */

  /* Progress bar value for IE10+ */
  background-image: -webkit-linear-gradient(left, #f44, blueviolet);
  -webkit-transform: rotate(90deg); /* Chrome, Safari, Opera */
  transform: rotate(90deg);
  width: 101vh;
  position: absolute;
  top: calc(50% - 3px);
  left: calc(-50vh + 58px);

  height: 63px;
  top: calc(50% - 30px);
  left: calc(-50vh + 26px);
  z-index: -1;
}

progress[value]::-webkit-progress-bar {
  background-color: white;

  background-color: blueviolet;
}

progress[value]::-webkit-progress-value {
  background-image: linear-gradient(
    left,
    rgba(255, 0, 0, 1) 0%,
    rgba(157, 69, 255, 1) 90%,
    blueviolet 90%
  );
  background: linear-gradient(
    90deg,
    rgba(255, 0, 0, 1) 0%,
    rgba(157, 69, 255, 1) 99%
  );
  background: linear-gradient(
    90deg,
    rgba(255, 0, 0, 1) 0%,
    rgba(173, 0, 194, 1) 71%,
    rgba(151, 0, 247, 1) 98%,
    blueviolet 100%
  );
}

table {
  border-collapse: collapse;
}

.fixed {
