.container {
  max-width: 800px;
  margin: auto;
}
.container-wide {
  max-width: 1000px;
  margin: 48px auto;
}
@media (max-width: 1240px) {
  .container-wide {
    margin-left: 102px;
    margin-right: 12px;
  }
}

.blog-text {
  background-color: white;
  padding: 48px;
  border-radius: 8px;
  background-color: #f8f8f8;
  margin: 12px auto;
}
@media (max-width: 1112px) {
  .blog-text {
    margin-left: 102px;
    margin-right: 12px;
  }
}
