.square {
  background-color: #ff2da4;
  height: 140px;
  width: 140px;
  color: white;
}
.framed {
  border: 16px solid var(--primary);
}
.padding {
  padding: 16px;
}
.margin {
  margin: 16px;
}
.flex {
  display: -webkit-flex;
  display: flex;
}

.box {
  min-width: 240px;
  max-width: 400px;
  padding: 36px;
  margin: 12px;
  text-align: center;
  background-color: var(--secondary);
  color: white;
  border-radius: 12px;
  box-sizing: border-box;
  -webkit-flex: 1 1;
          flex: 1 1;
  background-image: linear-gradient(
    135deg,
    var(--primary) 50%,
    transparent 50%
  );
  background-size: 100px 100px; /* some initial size to get the slanted appearance */
  background-position: -50px -50px; /* negative positioning to hide it initially */
  background-repeat: no-repeat;
  transition: all ease 0.8s;
}

@media (max-width: 1080px) {
  .box {
    margin: 4px;
  }
}
.box:hover {
  background-size: 200% 200%; /* 200% because gradient is colored only for 50% */
  background-position: 0px 0px; /* bring it fully into view */
  color: #fff;
}

.box p {
  font-size: 12px;
  text-decoration: none !important;
}
a.box p {
  text-decoration: none;
}
.box-container {
  margin: -12px;
  box-sizing: border-box;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
@media (max-width: 1080px) {
  .box-container {
    margin: -4px;
  }
}
.box img {
  width: 100px;
}
.box p,
.box img,
.box h5 {
  margin: 12px auto;
}

.box-container a {
  text-decoration: none;
}



